<template>
  <md-table @sort="sortRepositories">
    <md-table-header>
      <md-table-row>
        <md-table-head md-sort-by="name">
          Name
        </md-table-head>
        <md-table-head>
          Description
        </md-table-head>
        <md-table-head md-sort-by="stargazers_count">
          Number of Stars
        </md-table-head>
        <md-table-head md-sort-by="forks_count">
          Number of Forks
        </md-table-head>
        <md-table-head md-sort-by="open_issues_count">
          Number of Open Issues
        </md-table-head>
        <md-table-head md-sort-by="language">
          Language
        </md-table-head>
        <md-table-head md-sort-by="size">
          Size
        </md-table-head>
      </md-table-row>
    </md-table-header>
    <md-table-body>
      <md-table-row v-for="repository in getRepositories" v-bind:key="repository.id">
        <md-table-cell>
          <a :href="repository.html_url" target="_blank">
            {{repository.name}}</a>
        </md-table-cell>
        <md-table-cell>
          {{repository.description}}
        </md-table-cell>
        <md-table-cell>
          {{repository.stargazers_count}}
        </md-table-cell>
        <md-table-cell>
          {{repository.forks_count}}
        </md-table-cell>
        <md-table-cell>
          {{repository.open_issues_count}}
        </md-table-cell>
        <md-table-cell>
          {{repository.language}}
        </md-table-cell>
        <md-table-cell>
          {{repository.size}}kb
        </md-table-cell>
      </md-table-row>
      <md-table-row v-if="getRepositories.length === 0">
        <md-table-cell colspan="7">
          No Repositories found...
        </md-table-cell>
      </md-table-row>
    </md-table-body>
  </md-table>
</template>

<script>
export default {
  name: "repositories",
  mounted() {
    //this.$emit.closeSidenav();
    this.$emit('closeSidenav');
  },
  methods: {
    sortRepositories(sorting) {
      this.getRepositories.sort((a, b) => {
        if (a[sorting.name] > b[sorting.name]) {
          return sorting.type === 'asc' ? 1 : -1;
        }
        if (a[sorting.name] < b[sorting.name]) {
          return sorting.type === 'asc' ? -1 : 1;
        }

        return 0;
      })
    }
  },
  computed: {
    getRepositories() {
      return this.$store.getters.getRepositories;
    }
  }
}
</script>

<style>

</style>



